import React, { useState } from 'react'
import { connect } from 'react-redux'


const Landing = ({ setLocation, location, history, setPassword, password }) => {

    return (
        <div>
            <h3>Where you want to check into?</h3>
            <input
                type="text"
                name="location"
                onChange={e => setLocation(e.target.value)}
                value={location}
            />
            <br />
            <button
                onClick={() => history.push('/checkin')}
                style={{
                    padding: '10px 20px',
                    borderRadius: '5px'
                }}
            >Check In!</button>
            <br />
            <input
                type="text"
                onChange={e => setPassword(e.target.value)}
                value={password} />
        </div>
    )
}

const mapStateToProps = state => ({
    location: state.location.location,
    password: state.location.password
})

const mapDispatchToProps = dispatch => ({
    setLocation: location => dispatch({ type: 'CHANGE_LOCATION', payload: location }),
    setPassword: password => dispatch({ type: 'SET_PASSWORD', payload: password })
})

export default connect(mapStateToProps, mapDispatchToProps)(Landing)