import React from 'react'
import './CheckInCard.css'
import GreenTick from '../images/successpage-tickgreen-icon.svg'
import Success from '../images/successpage-icon.svg'
import CheckoutIcon from '../images/checkout-icon.svg'
import { connect } from 'react-redux'


const CheckInCard = ({ checkInLocation, password }) => {

  if(password !== 'zzz'){
    return (<div>Access denied</div>)
  }

  const getDate = () => {
    const months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sept',
      'Oct',
      'Nov',
      'Dec'
    ]

    const d = new Date();
    const day = d.getDate();
    const month = months[d.getMonth()];
    const year = d.getFullYear();
    const hours = d.getHours();
    const minutes = d.getMinutes();

    const apm = hours > 11 ? 'PM' : 'AM'

    const displayHours = hours > 11 ? hours - 12 : hours

    const dateString = `${day} ${month} ${year}, ${displayHours}:${minutes} ${apm}`
    return dateString
  }

  return (
    <div className="container">
      <div className="card">
        <div className="card-header">
          <img
            src={GreenTick}
            className="green-tick"
          />
          <img
            src={Success}
            className="success-text"
          />
        </div>

        <div className="card-body">
          <div className="on-text">You have successfully checked in on</div>
          <h3 className="date-text">{getDate()}</h3>
          <div className="place-text">{checkInLocation.toUpperCase()}</div>
        </div>


      </div>
      <div className="footer">
        <span className="thanks-text">Thank you for keeping Singapore safe!</span>
        <span className="screenshot-text">Screenshot this pass for record and donâ€™t forget to check out when you leave.</span>
      </div>

      <div>
        <a href="#" className="checkout-btn">Checkout <img src={CheckoutIcon} className="checkout-icon" /></a>
      </div>
    </div>

  )
}


const mapStateToProps = state => ({
  checkInLocation: state.location.location,
  password: state.location.password
})

export default connect(mapStateToProps)(CheckInCard)